<template>
	<view class="mine" :style="{height:containerHeight}" v-if="refresh" @touchmove.prevent>
		<view class="mine-top-view">
			<image :src="userInfo.avatar" class="touxiang" @click="changeAvatar"></image>
			<view class="touxiang-infor">
				<view>
					<view>{{userInfo.realname}} </view>
					<view style="color: #9f9f9f;">{{userInfo.telephone}} </view>
				</view>
			</view>
		</view>
		<view class="button-list">
			<view class="list-part" @click="updatePwd">
				<view style="display: flex;width: 50%;">
					<image src="../../static/qa/updatePassword.png" class="img"></image>
					<text>Change Password</text>
				</view>
				<uni-icons style="" type="right" size="20" color="#c9c9c9"></uni-icons>
			</view>

			<view class="list-part">
				<view style="display: flex;width: 50%;">
					<image src="../../static/qa/about.png" class="img"></image>
					<text>About</text>
				</view>
				<view style="color: #9f9f9f;margin-right:10% ;">Version {{version}}
					<!-- <uni-icons style="" type="right" size="20" color="#c9c9c9"></uni-icons> -->
				</view>
			</view>

		</view>

		<view class="logout" @click="logOut">
			<text>Log Out</text>
		</view>
	</view>
</template>

<script>
	import {
		ACCESS_TOKEN,
		USER_NAME,
		USER_INFO,
	} from "@/common/util/constants"
	import api from "@/api/api";
	export default {
		data() {
			return {
				containerHeight: '800px',
				userInfo: {
					// userName:this.$store.getters.username,
					userName: "Cao,Yuxi",
					telephone: "18344448888",
					avatar:"../../static/qa/defaultAvatar.jpg"
				},
				user: {},
				refresh: false,
				version: '1.0'
			}
		},
		onLoad() {
			// const info = uni.getSystemInfoSync();
			// if (info.uniPlatform.toLowerCase() === 'app') {
			//     plus.navigator.setStatusBarStyle("dark")
			// }
			this.refresh = true
			this.getScreenHeight()
			this.user = Object.assign({}, uni.getStorageSync(USER_INFO))
			this.userInfo.userName = this.user.username
			this.userInfo.realname = this.user.realname
			this.userInfo.telephone = this.user.phone
			if(this.user.avatar){
				console.log("头像："+this.user.avatar)
				this.userInfo.avatar = this.$apiUrl + '/sys/common/downloadFile?url=' + this.user.avatar
			}
		},
		onShow() {
			// const info = uni.getSystemInfoSync();
			// if (info.uniPlatform.toLowerCase() === 'app') {
			//     plus.navigator.setStatusBarStyle("dark")
			// }
		},
		methods: {
			changeAvatar() {
				let that = this
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定上传的图片是否压缩还是原图 默认全有
					sourceType: ['album', 'camera'], //album 从相册选图，camera 使用相机
					success: function(res) {
						const uploadTask = uni.uploadFile({
							url: that.$apiUrl + '/sys/common/upload',
							filePath: res.tempFilePaths[0],
							biz: 'temp',
							name: 'file',
							formData: {
								'biz': 'temp'
							},
							header: {
								"Content-Type": "multipart/form-data"
							},
							header: {
								/* 这个地方每次都调用 */
								"X-Access-Token": uni.getStorageSync('token')
							},
							success: function(res) {
								let data = JSON.parse(res.data)
								if (data && data.message) {
									let params = {
										avatar: data.message,
										username: that.userInfo.userName
									}
									that.$http.put(that.$apiUrl + '/sys/user/updateAvatar', params)
										.then((res) => {
											if (res.data.success) {
												uni.showToast({
													icon: 'none',
													title: 'Successfully. '
												});
												that.$set(that.userInfo, 'avatar',
													`${that.$apiUrl}/sys/common/downloadFile?url=${data.message}`
												)
												let userInfo = uni.getStorageSync(
													'login_user_info');
												uni.setStorageSync('login_user_info', JSON
													.stringify({
														...userInfo,
														data: {
															...userInfo.data,
															avatar: data.message
														}
													}))
												that.refresh = false
												that.refresh = true //强制渲染页面
											} else {
												uni.showToast({
													icon: 'error',
													title: 'Modification Failure'
												});
											}
										})
								}
								console.log(data)
							},
						});
						uploadTask.onProgressUpdate(function(res) {
							console.log("上传进度：" + res.progress);
							console.log("已经上传的进度：" + res.totalBytesSent);
							console.log("预计需要上传的数据总长度：" + res.totalBytesExpectedToSend);
						});
					}
				});
			},
			updatePwd() {
				console.log("修改")
				let that = this
				uni.navigateTo({
					url: '../mine/updatepwd?data=' + JSON.stringify(that.userInfo),
					fail: function(e) {
						console.log(e)
					}
				})
			},
			logOut() {
				api.logout().then(
					res => {
						uni.clearStorageSync()
						uni.reLaunch({
							url: '/pages/login/login'
						})
					})
			},
			getScreenHeight() {
				uni.getSystemInfo({
					success: (res) => {
						this.containerHeight = res.windowHeight + 'px';
						console.log(this.containerHeight)
					}
				});
			},
		}
	}
</script>

<style lang="scss" scoped scoped>
	page,
	view {
		/* display: flex; */
	}

	page {

		min-height: 100%;
		background-color: #FFFFFF;

	}

	.mine {
		width: 100%;
		background-color: #f2f2f2;
		position: fixed;
		top: env(safe-area-inset-top); /* 兼容 iOS 11.0-11.4 */
		top: constant(safe-area-inset-top); /* 兼容 iOS 12.0-12.1 */
		left: 0;
		touch-action: none;
	// 	padding-top: env(safe-area-inset-top);
	// 	/* 兼容 iOS 11.0-11.4 */
	// 	padding-top: constant(safe-area-inset-top);
	// 	/* 兼容 iOS 12.0-12.1 */
	}

	.mine-top-view {
		width: 100%;
		height: 25%;
		background-color: #FFFFFF;
		box-sizing: border-box;
		padding: 20% 17px 50px 17px;
		display: flex;

		.touxiang {
			width: 128upx;
			height: 128upx;
			display: flex;
			border-radius: 15px;
			margin-right: 34upx;
		}

		.touxiang-infor {
			flex: 1;
			display: flex;
		}
	}

	.button-list {
		// display: block;
		width: 100%;
		height: 58%;
		margin-top: 16upx;
		box-sizing: border-box;

		.list-part {
			height: 12%;
			background-color: #FFFFFF;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 2%;

			.img {
				margin: 2% 5% 0 10%;
				width: 20px;
				height: 20px;
			}
		}
	}

	.logout {
		height: 7.5%;
		background-color: #FFFFFF;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 2%;
	}
</style>